/* Mind Map Styles */

.mindmap-app {
  font-family: 'Inter', 'Roboto', 'Helvetica', 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Canvas basic styles */
.mindmap-canvas {
  background: #fafafa;
  position: relative;
}

/* node styles */
.mindmap-node {
  transition: all 0.3s ease;
}

.mindmap-node rect {
  transition: all 0.3s ease;
  cursor: pointer;
}

.mindmap-node rect:hover {
  cursor: pointer;
}

.mindmap-node text {
  font-family: 'Inter', sans-serif;
  font-weight: 500;
  transition: opacity 0.3s ease;
}

/* Connection line styles */
.connection-line {
  transition: all 0.3s ease;
}

.connection-line:hover {
  stroke-width: 4;
  opacity: 1;
  stroke: #3b82f6;
}

/* Node appear animation */
@keyframes nodeAppear {
  from {
    opacity: 0;
    transform: scale(0.8);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

/* Grid pattern */
.grid-pattern {
  opacity: 0.3;
}

/* Selected element highlight */
.selected-node {
  filter: drop-shadow(0 0 12px rgba(245, 158, 11, 0.6));
}

/* Tooltip styles */
.node-tooltip {
  position: absolute;
  background: rgba(17, 24, 39, 0.9);
  color: white;
  padding: 8px 12px;
  border-radius: 6px;
  font-size: 12px;
  pointer-events: none;
  z-index: 1000;
  backdrop-filter: blur(8px);
}

/* Zoom controls */
.zoom-controls {
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

/* mindmap-style node level colors */
.node-level-0 {
  fill: #6366f1;
}

.node-level-1 {
  fill: #8b5cf6;
}

.node-level-2 {
  fill: #ec4899;
}

.node-level-3 {
  fill: #f59e0b;
}

/* Highlight effects */
.node-highlighted {
  filter: drop-shadow(0 4px 12px rgba(59, 130, 246, 0.5));
}

.node-dimmed {
  opacity: 0.3;
  transition: opacity 0.3s ease;
}

.connection-highlighted {
  stroke: #3b82f6;
  stroke-width: 3;
  opacity: 1;
  filter: drop-shadow(0 2px 4px rgba(59, 130, 246, 0.3));
}

.connection-dimmed {
  opacity: 0.2;
  transition: opacity 0.3s ease;
}

/* Responsive design */
@media (max-width: 768px) {
  .mindmap-node text {
    font-size: 11px;
  }
  
  .grid-pattern {
    opacity: 0.2;
  }

  .mindmap-app .MuiContainer-root {
    padding: 8px;
  }
  
  .mindmap-app .MuiGrid-item {
    padding: 4px;
  }
}

/* Dark mode support */
@media (prefers-color-scheme: dark) {
  .mindmap-canvas {
    background: #1f2937;
  }
  
  .connection-line {
    stroke: #9ca3af;
  }
  
  .grid-pattern {
    stroke: #374151;
  }
}

/* Accessibility support */
@media (prefers-reduced-motion: reduce) {
  .mindmap-node,
  .connection-line {
    animation: none;
    transition: none;
  }
}

/* High contrast support */
@media (prefers-contrast: high) {
  .mindmap-node rect {
    border-width: 2px !important;
  }
  
  .connection-line {
    stroke-width: 3px !important;
    opacity: 1 !important;
  }
}

/* Dark mode preparation */
.mindmap-app.dark-mode {
  background-color: #121212;
  color: #ffffff;
}

.mindmap-app.dark-mode .mindmap-canvas {
  background: 
    radial-gradient(circle at 1px 1px, rgba(255,255,255,0.1) 1px, transparent 0);
  background-size: 20px 20px;
}
